import estimateYieldModel from '@/components/farmdetail/estimateYieldModel';
import farmIncome from '@/common/data/jiujiang-data.js'; // ä¹æ±Ÿ æµ‹è¯•æœæ­£å¼æœ éœ€ç”¨çš„å‡æ•°æ®
// const inputs = [{inputDate: 1292083200000, missionInputVOList: [{agriResName: 'å“ˆå“ˆå“ˆ', totalAmount: '200', specUnit: 'å…¬æ–¤', productor: 'å‚å®¶åç§°',},],},];
const color = ['#F0C87B', '#A6D889', '#DD90CB', '#9173FE', '#4DCBFF', '#1DE9B6', '#ca8622', '#bda29a', '#6e7074', '#546570', '#c4ccd3'];
const sales = [];
const xData = ['ä¸€æœˆ', 'äºŒæœˆ', 'ä¸‰æœˆ', 'å››æœˆ', 'äº”æœˆ', 'å…­æœˆ'];
const data = [220, 182, 191, 234, 290, 330];
const barData = xData.map((item, index) => {
	return {
		name: item,
		value: data[index],
		itemStyle: {
			color: color[index]
		}
	};
});
const barOption = {
	tooltip: {
		show: true,
		trigger: 'item',
		textStyle: {
			fontSize: 12,
			color: '#ffffff'
		}
	},
	title: {
		text: 'è¿‘åŠå¹´é”€å”®é¢',
		textStyle: {
			color: '#02E4EE',
			fontSize: 16,
			fontWeight: 'bold'
		},
		subtextStyle: {
			color: '#6DC1CB',
