			fontWeight: 400
		},
		left: 'center',
		padding: [17, 0, 0, 0],
		itemGap: 10,
		textVerticalAlign: 'top' // subtext: 'å•ä½ï¼šä¸‡å…ƒ'
	},
	grid: {
