			type: 'bar',
			barWidth: 24,
			data: barData
		}
	]
};
/* å†—ä½™ä»£ç åç­‰æµ‹è¯•æ‰¹å‡†åˆ é™¤ğŸ”ª */
const _farmIncome = (() => {
	const _o = {};
	for (const [key, value] of Object.entries(farmIncome)) {
		const o = {};
		value.forEach(item => {
			o[item.incomeDateStr] = item;
		});
		_o[key] = o;
	}
	return _o;
})();

export default {
	name: 'farmDetailsTabs',
	data() {
		return {
			// é”€å”®é¢
			sales: null,
			salesBar: deepCopy(barOption), // äº§é‡
			yield: null,
			yieldBar: deepCopy(barOption),
			farmRecordList: [],
			estimateYield: null,
			estimateYieldBar: deepCopy(barOption),
			generateFarmIntro: {
				// åŸºåœ°ä¿¡æ¯
				farmIntro: {}, // å…¬å¸ä¿¡æ¯
				companyInfo: {}, // åŸºåœ°è¿‡å»å…­ä¸ªæœˆçš„æ”¶å…¥æ€»è®¡
				farmIncomeVOS: [], // åŸºåœ°è¿‡å»å…­ä¸ªæœˆçš„é‡‡æ”¶æ€»è®¡
				govPickVOList: [],
				businessCategoryList: []
			},
			farmIntroContainer: null,
			farmRecordScroll: null,
			showCompanyIcon: false,
			viewPics: [],
			showViews: false,
			goodsSubjectUnit: null,
			estimateGoodsSubjectUnit: null, // å®é™…äº§å“å¤§ç±»åˆ—è¡¨
			goodsSubjectList: [
				/* { name: 'å†œä¸š', id: 'agri' }, { name: 'æ—ä¸š', id: 'forestry' }, { name: 'ç•œç‰§ä¸š', id: 'animal' }, { name: 'æ¸”ä¸š', id: 'fishery' } */
			],
			estimateGoodsSubjectList: [],
			yieldSubject: 'agri',
			estimateYieldSubject: 'agri',
			estimateYieldSubjectModel: 'agri',
			showEstimateYieldModel: false,
			curDate: null, // ç»è¥ä¸»ä½“ better-scrollå®ä¾‹
			busiCondContainer: null
		};
	},
	props: ['farminfo', 'farmId'],
	computed: {
		businessCategoryStr() {
			//å†œåœºç»è¥ç±»ç›®
			if (
				this.generateFarmIntro.businessCategoryList.length &&
				this.generateFarmIntro.farmIntro.businessCategoryIdList &&
				this.generateFarmIntro.farmIntro.businessCategoryIdList.length
			) {
				var nowFarmCateList = this.generateFarmIntro.farmIntro.businessCategoryIdList;
				var catelist = this.generateFarmIntro.businessCategoryList;
				var catenames = [];
				catelist.map(cate => {
					nowFarmCateList.includes(cate['id']) && catenames.push(cate['bizCategoryName']);
				});
				return catenames.join('ï¼Œ');
			} else {
				return '';
			}
		}
	},
	created() {
		this.initData();
	},
	async mounted() {
		console.log(this.generateFarmIntro);
		await this.getCropSubject(); // tab1 è·å–å®é™…ä½œç‰©å¤§ç±»åˆ—è¡¨
		this.$nextTick(async () => {
			this.farmIntroContainer = _initBScroll(this.$refs.farmIntroContainer, {
				scrollX: false,
				freeScroll: false,
				click: false
			});
			this.farmRecordScroll = _initBScroll(this.$refs.farmRecordScroll, {
				scrollX: false,
				freeScroll: false,
				click: false
			});
			/*this.busiCondContainer = _initBScroll(this.$refs.busiCondContainer, {
				scrollX: false,
				freeScroll: false,
				click: false
			});*/
		});
	},
	methods: {
		initData() {
			// è·å–åŸºåœ°ä»‹ç»
			this.generateFarmIntro = {
				// åŸºåœ°ä¿¡æ¯
				farmIntro: this.farminfo.farmDetailVO, // å…¬å¸ä¿¡æ¯
				companyInfo: this.farminfo.companyVO || {}, // åŸºåœ°è¿‡å»å…­ä¸ªæœˆçš„æ”¶å…¥æ€»è®¡
				farmIncomeVOS: this.farminfo.farmIncomeVOS, // åŸºåœ°è¿‡å»å…­ä¸ªæœˆçš„é‡‡æ”¶æ€»è®¡
				govPickVOList: this.farminfo.govPickVOList,
				businessCategoryList: this.farminfo.businessCategoryList
			};
			this.checkAuthCount = this.farminfo.inspectVOS.length; // æ£€æµ‹è®¤è¯æ•°é‡
			// å†œäº‹è®°å½• åˆ—è¡¨
		},

		// åˆå§‹åŒ– tab2
		async initTabWtwo() {
			await this.processEstimateCropSubject(); // tab2 æœªæ¥é¢„è®¡äº§é‡
			this.loadSalesBar(); // tab2 è¿‘åŠå¹´é”€å”®é¢
			await this.loadYieldBar(this.yieldSubject); // tab2 å®é™…äº§é‡
			this.loadEstimateYieldEchart(this.estimateYieldSubject); // åˆå§‹åŒ–æœªæ¥é¢„è®¡äº§é‡
		},

		// ç›‘æµ‹å¤§ç±»æ˜¯å¦éœ€è¦éœ€è¦æ¢ç®—å•ä½
		checkSubjectChangeUnit(subject) {
			const mapping = {
				agri: true,
				forestry: false,
				animal: false,
				fishery: true
			};
			return mapping[subject];
		},
		yieldXAxis(mode, format = 'yyyy.MM') {
			const curDateTimestamp = new Date(this.curDate).setDate(1);
			let curM = new Date(curDateTimestamp).getMonth();
			const xAxis = [];
			for (let i = 0; i < 6; i++) {
				xAxis.push(mode === 'add' ? dateFormat(new Date(curDateTimestamp).setMonth(curM++), format) : dateFormat(new Date(curDateTimestamp).setMonth(--curM), format));
			}
			// console.log(dateFormat(curDate.setMonth(11), 'yyyy.MM'));
			return xAxis;
		},

		// è·å–å®é™…ä½œç‰©å¤§ç±»åˆ—è¡¨
		async getCropSubject() {
			return this.$ajax
				.post(this.$api.public.farmCropSubject, {
					farmId: this.farmId
				})
				.then(({data: res}) => {
					this.curDate = res.curDate;
					if (res.respCode === 0) {
						this.goodsSubjectUnit = {};
						if (res.obj.list.length === 0) return false;
						this.goodsSubjectList = res.obj.list.map(item => {
							this.goodsSubjectUnit[item.cropSubjectIdentifier] =
								item.cropSubjectIdentifier === 'forestry' || item.cropSubjectIdentifier === 'animal' ? item.cropCategoryUnitAll : 'å¨';
							return {
								name: item.cropSubjectNickname,
								id: item.cropSubjectIdentifier
							};
						});
						this.yieldSubject = res.obj.list[0].cropSubjectIdentifier;
						return true;
					}
				});
		},

		// è·å–é¢„è®¡ä½œç‰©å¤§ç±»åˆ—è¡¨
		getEstimateCropSubject() {
			return new Promise((resolve, reject) => {
				this.$ajax
					.post(this.$api.public.getEstimateFarmCropSubject, {
						farmId: this.farmId
					})
					.then(({data: res}) => {
						if (res.respCode === 0) {
							resolve(res.obj);
						}
					})
					.catch(err => {
						console.log(err);
						reject(err);
					});
			});
		},

		// æœªæ¥é¢„è®¡äº§é‡
		async processEstimateCropSubject() {
			const res = await this.getEstimateCropSubject();
			this.estimateGoodsSubjectUnit = {};
			if (res.list.length === 0) return false;
			this.estimateGoodsSubjectList = res.list.map(item => {
				this.estimateGoodsSubjectUnit[item.cropSubjectIdentifier] =
					item.cropSubjectIdentifier === 'forestry' || item.cropSubjectIdentifier === 'animal' ? item.cropCategoryUnitAll : 'å¨';
				return {
					name: item.cropSubjectNickname,
					id: item.cropSubjectIdentifier
				};
			});
			this.estimateYieldSubject = res.list[0].cropSubjectIdentifier;
		},

		// è·å–è¿‘åŠå¹´çš„äº§é‡
		getOutput(subjectIdentifier) {
			return this.$ajax
				.post(this.$api.farmDetail.getOutputListByFarmId, {
					dateList: this.yieldXAxis('minus', 'yyyy-MM-dd').reverse(),
					farmId: this.farmId,
					subjectIdentifier
				})
				.then(({data: res}) => {
					if (res.respCode === 0) {
						return res.obj;
					}
				});
		},

		// è·å–æœªæ¥é¢„è®¡äº§é‡æ•°æ®è¡¨
		getEstimateYieldEchartsData() {
			return new Promise((resolve, reject) => {
				this.$ajax
					.post(this.$api.farmDetail.getPickForecastMonthByFarmId, {
						farmId: this.$route.params.farmid,
						subjectIdentifier: this.estimateYieldSubject
					})
					.then(({data: res}) => {
						if (res.respCode === 0) {
							resolve(res.obj);
						}
					})
					.catch(err => {
						console.log(err);
						reject(new Error(err));
					});
			});
		},

		// è¿‘åŠå¹´é”€å”®é¢
		loadSalesBar() {
			const unit = 'ä¸‡å…ƒ';
			const fakeData = _farmIncome[this.generateFarmIntro.farmIntro.farmId];
			this.salesBar.title.text = 'è¿‘åŠå¹´é”€å”®é¢';
			this.salesBar.title.subtext = `å•ä½ï¼š${unit}`;
			this.salesBar.xAxis.data = this.generateFarmIntro.farmIncomeVOS.map(item => {
				return dateFormat(item.incomeDate, 'yyyy.MM');
			});
			this.salesBar.xAxis.axisLabel.formatter = v => v; // `${v.split('.')[1]}\n${v.split('.')[0]}`;
			this.salesBar.series[0].data = this.generateFarmIntro.farmIncomeVOS.map((item, index) => {
				return {
					name: dateFormat(item.incomeDate, 'yyyy.MM'),
					value: (() => {
						if (fakeData) {
							return fakeData[item.incomeDateStr]
								? maxDecimalPlaces({
										value: fakeData[item.incomeDateStr].totalAmount / Math.pow(10, 4)
								  })
								: 0;
						}
						return maxDecimalPlaces({
							value: item.totalAmount / Math.pow(10, 4)
						});
					})(),
					itemStyle: {
						color: color[index]
					}
				};
			});
			this.salesBar.tooltip.formatter = v =>
				`${v.name}<br/>${
					unitTranslate({
						value: v.value,
						unit: 'å…ƒ',
						base: 10000
					}).formatter
				}`;
			this.salesBar.yAxis.axisLabel.formatter = v => unitTranslate({value: v}).formatter;
			this.sales = this.$echarts.init(this.$refs.sales);
			this.sales.setOption(this.salesBar);
		},

		// åˆå§‹åŒ–å®é™…äº§é‡
		async loadYieldBar(subjectIdentifier) {
			const unit = this.goodsSubjectUnit[subjectIdentifier] || 'å¨';
			const res = await this.getOutput(subjectIdentifier);
			this.yieldBar.title.text = 'è¿‘åŠå¹´å®é™…äº§é‡';
			this.yieldBar.xAxis.data = res.map(item => {
				return dateFormat(item.pickTime, 'yyyy.MM');
			});
			this.yieldBar.yAxis.name = `å•ä½ï¼š${unit}`;
			this.yieldBar.xAxis.axisLabel.formatter = v => v;
			this.yieldBar.series[0].data = res.map((item, index) => {
				return {
					name: dateFormat(item.pickTime, 'yyyy.MM'),
					value: maxDecimalPlaces({value: this.checkSubjectChangeUnit(subjectIdentifier) ? item.sumPickOutput / Math.pow(10, 3) : item.sumPickOutput}), // å…¬æ–¤ - å¨
					itemStyle: {
						color: color[index]
					}
				};
			});
			this.yieldBar.yAxis.axisLabel.formatter = v => unitTranslate({value: v}).formatter;
			this.yieldBar.tooltip.formatter = v =>
				`${v.name}<br/>${
					unitTranslate({
						value: v.value,
						unit
					}).formatter
				}`;
			// console.log(this.$refs.yield);
			this.yield = this.$echarts.init(this.$refs.yield);
			this.yield.setOption(this.yieldBar);
		},

		// åˆå§‹åŒ–æœªæ¥é¢„è®¡äº§é‡
		async loadEstimateYieldEchart(subjectIdentifier) {
			const res = await this.getEstimateYieldEchartsData();
			const unit = this.estimateGoodsSubjectUnit[subjectIdentifier] || 'å¨';
			this.estimateYieldBar.title.text = 'æœªæ¥é¢„è®¡äº§é‡';
			this.estimateYieldBar.xAxis.data = res.total.map(item => {
				return dateFormat(new Date(`${item.pickMonthStr}-01`), 'yyyy.MM');
			});
			this.estimateYieldBar.series[0].data = res.total.map((item, index) => {
				return {
					name: dateFormat(new Date(`${item.pickMonthStr}-01`), 'yyyy.MM'),
					value: maxDecimalPlaces({value: this.checkSubjectChangeUnit(subjectIdentifier) ? item.sumTotal / Math.pow(10, 3) : item.sumTotal}), // å…¬æ–¤ - å¨
					itemStyle: {
						color: color[index]
					}
				};
			});
			this.estimateYieldBar.yAxis.name = `å•ä½ï¼š${unit}`;
			this.estimateYieldBar.xAxis.axisLabel.formatter = v => v;
			this.estimateYieldBar.yAxis.axisLabel.formatter = v => unitTranslate({value: v}).formatter;
			this.estimateYieldBar.tooltip.formatter = v =>
				`${v.name}<br/>${
					unitTranslate({
						value: v.value,
						unit
					}).formatter
				}`;
			this.estimateYield = this.$echarts.init(this.$refs.estimateYield);
			this.estimateYield.setOption(this.estimateYieldBar);
			// æ£€æµ‹ä¸è®¤è¯è¶‹åŠ¿å›¾clickäº‹ä»¶
			this.estimateYield.on('click', r => {
				// è§¦å‘çˆ¶çº§å…³é—­å¼¹æ¡†ç­‰äº¤äº’
				this.$emit('closeAllDialogEv');
				setTimeout(() => {
					// é˜²æ­¢å¼¹æ¡†é‡å çš„æƒ…å†µ
					this.estimateYieldSubjectModel = this.estimateYieldSubject;
					this.showEstimateYieldModel = true;
				}, 200);
			});
		},
		handleTabs(tar) {
			// tab2 åˆå§‹åŒ–
			/*if (!this.initTabWtwo.init) {
				this.initTabWtwo.init = true;
				this.initTabWtwo();
			}*/
			this.$nextTick(() => {
				if (tar.name === 'busiCond') {
					//this.busiCondContainer.refresh();
				} else if (tar.name === 'input') {
					this.farmRecordScroll.refresh();
				}
			});
		},
		openViews(p) {
			this.showViews = true;
			this.viewPics = p;
		}
	},
	components: {
		farmRecord,
		views,
		estimateYieldModel
	}
};
</script>
<style scoped lang="scss">
@import '~styles/selectSkin_default';
@import '~styles/tabsSkin_default';
@import '~styles/scrollbarSkin_default';
@import '~styles/tooltipSkin_default';
@keyframes showani {
	0% {
		opacity: 0;
		margin-top: calc(50vh - 169px);
	}
	100% {
		opacity: 1;
		margin-top: calc(50vh - 269px);
	}
}
.tabs-container {
	position: absolute;
	opacity: 0;
	margin-top: calc(50vh - 369px);
	margin-left: 2px;
	animation: showani 0.5s 0.3s;
	animation-fill-mode: forwards;
}
.farm-intro {
	width: 100%;
	height: 100%;
	box-sizing: border-box;
	padding: 15px 18px 10px;
	.farm-intro-container {
		height: 100%;
		overflow: hidden;
		.farm-intro-wrap {
			> .info-item {
				padding: 15px;
				background-color: #061f36;
				font-size: 14px;
				line-height: 24px;
				color: #6dc1cb;
				font-weight: 400;
				b {
					color: #a1eff8;
					font-weight: bold;
				}
				.icon-pic-wrap {
					cursor: pointer;
					color: #15e9fc;
					.iconfont {
						font-size: 18px;
					}
				}
				+ .info-item {
					margin-top: 10px;
				}
			}
			div {
				&.info-item {
					padding: 0;
					.head-item {
						height: 42px;
						box-sizing: border-box;
						padding: 0 16px;
						display: flex;
						align-items: center;
						justify-content: space-between;
					}
					.content-item {
						padding: 0 15px;
						border-top: 2px solid #044c64;
						.content-lists {
							padding: 20px 0 15px;
							.list {
								margin: 15px 0;
								display: flex;
								align-items: center;
								&:first-child {
									margin-top: 0;
								}
								&:last-child {
									margin-bottom: 0;
								}
								.list-name {
									color: #6dc1cb;
									font-size: 14px;
									line-height: 16px;
									max-width: 150px;
									min-width: 0;
									white-space: nowrap;
									text-overflow: ellipsis;
									overflow: hidden;
									cursor: pointer;
									margin-top: -3px;
								}
								.list-line {
									flex: 1;
									height: 2px;
									background: url('~@/assets/images/dotted-line-bg.png') repeat-x;
									margin: 0 5px;
								}
								.list-des {
									font-size: 14px;
									color: #15e9fc;
									cursor: pointer;
									.iconfont {
										font-size: 20px;
										margin-right: 0px;
										vertical-align: -2px;
									}
								}
							}
						}
					}
				}
			}
		}
	}
}
.busi-cond {
	width: 100%;
	height: 100%;
	box-sizing: border-box;
	padding: 25px 18px 15px;
	.busi-cond-container {
		height: 100%;
		overflow: hidden;
		.busi-cond-wrap {
			& > .bar-item {
				width: 346px;
				height: 228px;
				background-color: rgba(6, 31, 54, 1);
				position: relative;
				+ .bar-item {
					margin-top: 22px;
				}
				.subject-select {
					position: absolute;
					top: 28px;
					right: 15px;
					max-width: 120px;
					height: 30px;
					z-index: 10;
					/deep/ .el-input__inner {
						width: 100%;
						text-align: right;
						border: 0 none !important;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						font-size: 12px;
					}
					&:hover {
						border: 0 none;
						/deep/ .el-input__inner {
							border: 0 none !important;
						}
					}
				}
				.chart {
					width: 346px;
					height: 228px;
				}
			}
		}
	}
}
.farmword-container {
	width: 346px;
	height: calc(100% - 30px);
	margin: 15px auto 15px;
	box-sizing: border-box;
	.farmword-wrap {
		height: 100%;
		overflow: hidden;
	}
}
/deep/ .bscroll-vertical-scrollbar {
	right: 6px !important;
}
</style>



